<template>
  <div class="row m-0">
    <div class="col-12 mt-5">
      <div class="row mt-5">
        <button
          type="button"
          @click="isprintModal = true"
          class="btn btn-outline-info mr-2 ml-2 mb-3"
        >اطبع</button>
        <base-button
          type="button"
          @click="isaddModal = true"
          class="btn btn-outline-success mr-2 ml-2 mb-3"
          icon="ni ni-fat-add"
        >اضافه منتج</base-button>
      </div>
      <b-modal :active.sync="isprintModal">
        <div class id="printMe">
          <div
            v-for="(prod ,index) in checkedProduct"
            :key="index"
            class="row m-0 justify-content-center"
          >
            <div class="col-12">
              <div class="row m-0 justify-content-center">
                <figure class="image image is-48x48">
                  <img src="@/assets/image/logo.jpg" alt="Placeholder image" />
                </figure>
              </div>

              <div class="row justify-content-center">السعر{{prod.outprice}}</div>
            </div>
            <barcode
              v-if="prod.id"
              :value="prod.id"
              :options="{ lineColor: '#444e',height:'100px' }"
              tag="img"
            >{{prod.id}}</barcode>
          </div>
        </div>

        <div class="row m-0 justify-content-center">
          <button v-print="printObj" type="button" class="btn btn-outline-info mr-2 ml-2 mb-3">
            <i class="fas fa-print"></i> اطبع
          </button>
        </div>
      </b-modal>
      <b-modal has-modal-card :active.sync="isaddModal">
        <form  @submit.prevent="addNewProuduct()" data-vv-scope="add" clas class="modal-card" style="width: auto" >
          <header class="modal-card-head">
            <p class="modal-card-title">اضافة</p>
          </header>
          <section class="modal-card-body" >
            <div class="col-12">
              <base-input
                v-model="product.name"
                placeholder="اسم المنتج "
                v-validate="'required'"
                name="name"
                :valid="!errors.has('name')"
              ></base-input>
              <span class="help is-danger">{{ errors.first('name') }}</span>
            </div>
            <div class="col-12">
              <base-input
                v-model="product.inprice"
                type="number"
                placeholder="السعر"
                v-validate="'required'"
                name="inprice"
                :valid="!errors.has('inprice')"
              ></base-input>
              <span class="help is-danger">{{ errors.first('inprice') }}</span>
            </div>
            <div class="col-12">
              <base-input
                v-model="product.outprice"
                placeholder="سعرالبيع "
                type="number"
                v-validate="'required'"
                name="outprice"
                :valid="!errors.has('outprice')"
              ></base-input>
              <span class="help is-danger">{{ errors.first('outprice') }}</span>
            </div>
            <div class="col-12">
              <base-input
                v-model="product.quantity"
                placeholder="الكمية"
                type="number"
                v-validate="'required'"
                name="quantity"
                :valid="!errors.has('quantity')"
              ></base-input>
              <span class="help is-danger">{{ errors.first('quantity') }}</span>
            </div>
          </section>
          <footer class="modal-card-foot">
            <button class="button m-1" type="button" @click="isaddModal=false">غلق</button>
            <button class="button is-success m-1" type="submit">اضافه</button>
          </footer>
        </form>
      </b-modal>
      <b-modal has-modal-card :active.sync="isupdateModal">
        <form class="modal-card" @submit.prevent="updateProduct()" style="width: auto" data-vv-scope="update">
          <header class="modal-card-head">
            <p class="modal-card-title">تعديل</p>
          </header>
          <section class="modal-card-body" >
            <div class="col-12">
              <base-input
                v-model="selectedproduct.name"
                placeholder="اسم المنتج "
                v-validate="'required'"
                name="nameup"
                :valid="!errors.has('nameup')"
              ></base-input>
              <span class="help is-danger">{{ errors.first('nameup') }}</span>
            </div>
            <div class="col-12">
              <base-input
                v-model="selectedproduct.inprice"
                type="number"
                placeholder="السعر"
                v-validate="'required'"
                name="inpriceup"
                :valid="!errors.has('inpriceup')"
              ></base-input>
              <span class="help is-danger">{{ errors.first('inpriceup') }}</span>
            </div>
            <div class="col-12">
              <base-input
                v-model="selectedproduct.outprice"
                placeholder="سعرالبيع "
                type="number"
                v-validate="'required'"
                name="outpriceup"
                :valid="!errors.has('outpriceup')"
              ></base-input>
              <span class="help is-danger">{{ errors.first('outpriceup') }}</span>
            </div>
            <div class="col-12">
              <base-input
                v-model="selectedproduct.quantity"
                placeholder="الكمية"
                type="number"
                v-validate="'required'"
                name="quantityup"
                :valid="!errors.has('quantityup')"
              ></base-input>
              <span class="help is-danger">{{ errors.first('quantityup') }}</span>
            </div>
          </section>
          <footer class="modal-card-foot">
            <button class="button m-1" type="button" @click="isupdateModal=false">غلق</button>
            <button class="button is-success m-1" type="submit">تعديل</button>
          </footer>
        </form>
      </b-modal>
    </div>
    <b-table
      class="w-100"
      v-if="products.length>0"
      :data="products"
      checkable
      :checkbox-position="checkboxPosition"
      :checked-rows.sync="checkedProduct"
      :columns="columns"
      icon-pack="fa"
      :paginated="isPaginated"
      :per-page="perPage"
      :current-page.sync="currentPage"
      :pagination-simple="isPaginationSimple"
      :pagination-position="paginationPosition"
      :default-sort-direction="defaultSortDirection"
      :sort-icon="sortIcon"
      :sort-icon-size="sortIconSize"
      default-sort="user.first_name"
      aria-next-label="Next page"
      aria-previous-label="Previous page"
      aria-page-label="Page"
      aria-current-label="Current page"
      :selected.sync="selectedproduct"
      @click="isupdateModal=true"
    >
      <b-input
        v-if="!props.column.numeric"
        slot="searchable"
        slot-scope="props"
        v-model="props.filters[props.column.field]"
        placeholder="ابحث"
        icon="magnify"
        size="is-small"
      />
    </b-table>
    <b-loading :is-full-page="true" :active.sync="isLoading" :can-cancel="true"></b-loading>
  </div>
</template>
<script>
import * as firebase from "firebase/app";
import * as moment from "moment";
import VueBarcode from "vue-barcode";

export default {
  components: {
    barcode: VueBarcode
  },
  data() {
    return {
      isupdateModal: false,
      isprintModal: false,
      isaddModal: false,
      printObj: {
        id: "printMe"
      },
      printing: false,
      checkedProduct: [],
      isLoading: false,
      checkboxPosition: "left",
      product: {
        id: "",
        name: "",
        inprice: "",
        outprice: "",
        quantity: ""
      },
      products: [],
      isPaginated: true,
      isPaginationSimple: false,
      paginationPosition: "bottom",
      defaultSortDirection: "asc",
      sortIcon: "arrow-up",
      sortIconSize: "is-small",
      currentPage: 1,
      perPage: 10,
      selectedproduct: {},
      columns: [
        {
          field: "id",
          label: "الكود",
          width: "100",
          numeric: false,
          searchable: true
        },
        {
          field: "inprice",
          label: "السعر",
          searchable: true
        },
        {
          field: "name",
          label: "الاسم",
          searchable: true
        },
        {
          field: "outprice",
          label: "سعر البيع",
          centered: true
        },
        {
          field: "quantity",
          label: "الكمية"
        }
      ]
    };
  },
  methods: {
    async getproducts() {
      var ref = firebase.database().ref("proudcts");
      var vm = this;
      this.isLoading = true;

      await ref.on("value", function(snapshot) {
        //  this.providers=snapshot.val()
        snapshot.forEach(obj => {
          vm.products.push({ ...obj.val() });
          vm.colms = Object.keys({ ...obj.val() });
        });
        vm.isLoading = false;
      });
    },
    updateProduct() {
      this.isLoading = true;

      this.$validator.validateAll("update").then(valid => {
        if (valid) {
          firebase
            .database()
            .ref("proudcts/" + this.selectedproduct.id)
            .set(this.selectedproduct);

          this.selectedproduct = {};
          this.$buefy.toast.open({
            message: "تمت تعديل المنتج بنجاح",
            type: "is-success"
          });
          this.isLoading = false;
          this.isupdateModal = false;
        } else {
          this.isLoading = false;

          this.$buefy.toast.open({
            duration: 1000,
            message: `من فضلك ادخل كل الحقول `,
            position: "is-bottom",
            type: "is-danger"
          });
        }
      });
    },

    addNewProuduct() {
      this.isLoading = true;

      this.$validator.validateAll("add").then(valid => {
        if (valid) {
          let database = firebase.database();
          let newprouduct = database.ref("proudcts").push();
          this.product.id = newprouduct.ref.key;

          newprouduct.set(this.product);
          this.isaddModal = false;
          this.isLoading = false;
          this.product = {};
          this.$buefy.toast.open({
            message: "تمت اضافة المنتج بنجاح",
            type: "is-success"
          });
        } else {
          this.isLoading = false;

          this.$buefy.toast.open({
            duration: 1000,
            message: `من فضلك ادخل كل الحقول `,
            position: "is-bottom",
            type: "is-danger"
          });
        }
      });
    }
  },
  mounted() {},
  created() {
    this.getproducts();
  }
};
</script>
<style></style>
